import React, { Component } from 'react'
import Header from '../header'
import RandomPlanet from '../random-planet'
import { SwapiServiceProvider } from '../swapi-service-context'
import SwapiService from '../../services/swapi-service'
import DummySwapiService from '../../services/dummy-swapi-service'
import { PeoplePage, PlanetsPage, StarshipsPage } from '../pages'
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import './app.css'
import StarshipDetails from '../sw-components/starship-details'

export default class App extends Component {

  state = {
    swapiService: new SwapiService()
  };

  onServiceChange = () => {
    this.setState(({ swapiService }) => {

      const Service = swapiService instanceof SwapiService ?
        DummySwapiService : SwapiService;

      return {
        swapiService: new Service()
      };
    });
  };

  render() {
    return (
      <SwapiServiceProvider value={this.state.swapiService}>
        <Router>
          <Switch>
            <React.Fragment>
              <div className="stardb-app">
                <Header onServiceChange={this.onServiceChange} />
                <RandomPlanet />

                <Route path='/StarDB/' render={() => <h2>Welcome to StarDB</h2>} exact />
                <Route path='/StarDB/people/:id?' component={PeoplePage} />
                <Route path='/StarDB/planets' component={PlanetsPage} />
                <Route path='/StarDB/starships' exact component={StarshipsPage} />
                <Route path='/StarDB/starships/:id'
                  render={({ match }) => {
                    return <StarshipDetails itemId={match.params.id} />
                  }} />

                <Route path='/404' render={() => <h2>Page not found</h2>} />
                <Redirect to='/404' />
              </div>
            </React.Fragment>
          </Switch>
        </Router>

      </SwapiServiceProvider>
    );
  }
}
