import React, { Component } from 'react';
import ErrorIndicator from '../indicators'
import { ReactComponent as Loading } from '../indicators/loading.svg';
import './item-details.css';
import SwapiService from '../../services/swapi-service';

const Record = ({ item, field, label }) => {
  return (
    <li className="list-group-item">
      <span className="term">{label}</span>
      <span>{item[field]}</span>
    </li>
  )
}

export {
  Record
};

const ItemView = ({ item, image, children }) => {
  if (!item) {
    return <span>Select item from a list</span>
  }

  return (
    <React.Fragment>
      <img className="item-image"
        src={image}
        alt={item.name}
        onError={(e) => {
          e.target.onerror = null;
          e.target.src = "https://starwars-visualguide.com/assets/img/placeholder.jpg"
        }} />
      <div className="card-body">
        <h4>{item.name}</h4>
        <ul className="list-group list-group-flush">
          {
            React.Children.map(children, (child) => {
              return React.cloneElement(child, { item })
            })
          }
        </ul>
      </div>
    </React.Fragment>
  )
}

export default class ItemDetails extends Component {

  swapiService = new SwapiService()

  state = {
    load: false,
    error: false,
    item: null,
    image: null
  }

  componentDidMount() {
    this.updateItem()
  }

  componentDidUpdate(prevProps) {
    if (this.props.itemId !== prevProps.itemId ||
      this.props.getData !== prevProps.getData) {
      this.updateItem()
    }
  }

  onItemUpdated = (item) => {
    const { getImageUrl } = this.props
    this.setState({
      item,
      image: getImageUrl(item),
      load: false,

    })
  }

  onError = (err) => {
    this.setState({
      error: true,
      loading: false
    })
  }

  updateItem() {
    const { itemId, getData } = this.props
    if (!itemId) {
      return
    }
    this.setState({ load: true })

    getData(itemId)
      .then((this.onItemUpdated))
      .catch(this.onError)
  }

  render() {

    const { item, load, error, image } = this.state
    const hasData = !(load || error)
    const errorMessage = error ? <ErrorIndicator /> : null
    const loading = load ? <Loading /> : null

    const content = hasData ? <ItemView
      item={item}
      image={image}
      children={this.props.children} /> : null

    return (
      <div className="item-details card">
        {errorMessage}
        {loading}
        {content}
      </div>
    )
  }
}
