import React, { Component } from 'react';

import SwapiService from '../../services/swapi-service'
import ErrorIndicator from '../indicators'
import { ReactComponent as Loading } from '../indicators/loading.svg';
import './random-planet.css';

const PlanetView = ({ planet }) => {
  const { id, planetName, population, rotationPeriod, diameter } = planet
  return (
    <React.Fragment>
      <img className="planet-image"
        src={`https://starwars-visualguide.com/assets/img/planets/${id}.jpg`}
        alt={planetName}
        onError={(e) => {
          e.target.onerror = null;
          e.target.src = "https://starwars-visualguide.com/assets/img/placeholder.jpg"
        }}
      />
      <div>
        <h4>{planetName}</h4>
        <ul className="list-group list-group-flush">
          <li className="list-group-item">
            <span className="term">Population</span>
            <span>{population}</span>
          </li>
          <li className="list-group-item">
            <span className="term">Rotation Period</span>
            <span>{rotationPeriod}</span>
          </li>
          <li className="list-group-item">
            <span className="term">Diameter</span>
            <span>{diameter}</span>
          </li>
        </ul>
      </div>
    </React.Fragment>
  )
}

export default class RandomPlanet extends Component {

  swapiService = new SwapiService()

  state = {
    planet: {},
    loading: true,
    error: false,
  }

  componentDidMount() {
    this.updatePlanet()
    this.interval = setInterval(this.updatePlanet, 8000)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  randomInteger(min, max) {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
  }

  onPlanetLoaded = (planet) => {
    this.setState({
      planet,
      loading: false
    })
  }

  onError = (err) => {
    this.setState({
      error: true,
      loading: false
    })
  }

  updatePlanet = () => {
    const id = this.randomInteger(2, 61)
    this.swapiService
      .getPlanet(id)
      .then(this.onPlanetLoaded)
      .catch(this.onError)
  }

  render() {
    const { planet, loading, error } = this.state

    const hasData = !(loading || error)
    const load = loading ? <Loading /> : null
    const content = hasData ? <PlanetView planet={planet} /> : null
    const errorMessage = error ? <ErrorIndicator /> : null

    return (
      <div className="random-planet jumbotron rounded">
        {load}
        {content}
        {errorMessage}
      </div>
    );
  }
}

